$black-bg: #000000;

$black-title: #222222;

$blue-title: #1eacc7;

$white: #ffffff;

$red: #FF5656;

@mixin btn {
    background: linear-gradient(0deg, $blue-title 0%, #4ce2ff 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border-style: none;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: $white;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);

    &:hover {
        background: $blue-title;
    }
}

@mixin flex-c-c {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    font-family: "Roboto", sans-serif;
}

a:hover {
    cursor: pointer;
}

body {
    position: relative;
}

nav {
    position: fixed;
    background-color: #212121;
    height: 50px;
    width: 100%;
    top: 0;
    z-index: 2;

    .menu {
        display: flex;
        padding: 0 71px 0 66px;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        margin: 0;

        &_item {
            list-style-type: none;

            .menu_link {
                font-weight: 300;
                font-size: 15px;
                color: $white;
                text-align: center;
                text-decoration-line: none;
            }
        }

        &_line {
            background: rgba(0, 0, 0, 0.0001);
            mix-blend-mode: soft-light;
            border: 1px dashed $black-bg;
            list-style-type: none;
            width: 2px;
            min-height: 31px;
        }
    }
}

.subheader {
    height: 85px;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    z-index: 1;
    top: 50px;

    &_row {
        min-height: 85px;
    }

    &_logo {
        &_link {
            display: block;
        }
    }

    &_logo_text {
        font-weight: 100;
        font-size: 13px;
        color: $white;
        margin-top: 5px;
    }

    &_link_tel {
        display: block;
        font-weight: 400;
        font-size: 22px;
        text-align: right;
        text-transform: uppercase;
        color: $white;
        text-decoration-line: none;
        margin-top: 5px;
    }

    &_call {
        margin: 0;
        font-weight: 300;
        font-size: 12px;
        line-height: 14px;
        text-align: right;
        color: $white;
    }

    &_btn {
        @include btn;
        font-size: 14px;
        width: 172px;
        height: 46px;

    }
}

.promo {
    background: url("../img/uber_bg.png") center center/cover no-repeat;
    min-height: 799px;
    padding-top: 142px;
    padding-bottom: 186px;
    margin-top: 26px;
    
    

    &_header {
        font-weight: 900;
        font-size: 60px;
        text-align: center;
        text-transform: uppercase;
        color: $white;
        margin: 0;
    }

    &_subheader {
        font-weight: 700;
        font-size: 36px;
        text-align: center;
        text-transform: uppercase;
        color: $white;
        margin: 0;
    }

    &_text {
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: $white;
        margin-top: 16px;
    }

    &_btn {
        @include btn;
        font-size: 18px;
        width: 259px;
        height: 63px;
        display: block;
        margin: 115px auto 0 auto;
    }
}

.benefits {
    min-height: 849px;
    padding: 18px 0 64px 0;

    &_wraper {
        @include flex-c-c;
        margin-top: 16px;
    }

    &_round {
        @include flex-c-c;
        width: 116px;
        height: 116px;
        border-radius: 100%;
        background: $blue-title;
        margin-left: 33px;
    }

    &_descr {
        max-width: 372px;
        font-weight: 300;
        font-size: 15px;
        line-height: 21px;
        color: #202020;
        margin: auto 0 auto 25px;
    }

    &_descr_title {
        font-weight: 400;
        font-size: 17px;
        color: $blue-title;
        margin: 0;
    }
}

.label {
    font-weight: 400;
    font-size: 11px;
    text-align: center;
    text-transform: uppercase;
    color: $white;
    width: 115px;
    height: 27px;
    background: $black-bg;
    margin: 0 auto;
    @include flex-c-c;
}

.label_white {
    background: $white;
    color: #070716;
}

.title {
    margin-top: 32px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: $black-title;
}

.title_white {
    color: $white;
    margin-top: 26px;
}

.subtitle {
    font-weight: 400;
    font-size: 17px;
    text-align: center;
    color: $black-title;
    margin-top: 1px;
}

.subtitle_white {
    color: $white;
}

.mobileapp {
    padding: 48px 0 80px 0;
    min-height: 961px;
    background: url("../img/bg_mob_application.png") center center/cover no-repeat;

    .row {
        margin-top: 30px;
    }


    &_block {
        padding-top: 85px;
        height: 280px;
        width: 320px;
        margin-bottom: 19px;
        position: relative;

        .mobileapp_plus {
            width: 26px;
            height: 26px;
            background: $black-bg;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 6px;
            position: absolute;
            bottom: 35px;
            transform: translateX(-50%);
            left: 50%;
            cursor: pointer;

            &:before {
                content: '';
                display: block;
                background: $blue-title;
                width: 14px;
                height: 2px;
            }

            &:after {
                content: '';
                display: block;
                background: $blue-title;
                width: 2px;
                height: 14px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }


        }

        .mobileapp_desr {
            font-weight: 400;
            font-size: 24px;
            text-align: center;
            text-transform: uppercase;
            color: $white;
        }
    }

    &_block_1 {
        background: url("../img/mob_application1.png") center center/cover no-repeat;
    }

    &_block_2 {
        background: url("../img/mob_application2.png") center center/cover no-repeat;
    }

    &_block_3 {
        background: url("../img/mob_application3.png") center center/cover no-repeat;
    }

    &_block_4 {
        background: url("../img/mob_application4.png") center center/cover no-repeat;
    }

    &_block_5 {
        background: url("../img/mob_application5.png") center center/cover no-repeat;
    }

    &_block_6 {
        background: url("../img/mob_application6.png") center center/cover no-repeat;
    }
}

.choice {
    min-height: 870px;
    padding: 59px 0 137px 0;
    background: #070716;
    position: relative;
    z-index: -2;


    &_descr {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        text-align: center;
        color: $black-title;
        width: 327px;
        margin: 0 auto;
    }

    &_bg_bottom {
        position: absolute;
        bottom: 0;
        z-index: -1;
        width: 100%;
    }

    &_auto {
        display: block;
        margin: 54px auto 0 auto;
       
    }
}

.conditions {
    padding: 68px 0 87px 0;

    &_wraper {
        margin: 33px auto;

        .conditions_title {
            font-style: normal;
            font-weight: 400;
            font-size: 22px;
            color: #1CA8C3;
        }

        .red {
            color: #FF5656;
        }

        .conditions_descr {
            font-weight: 300;
            font-size: 17px;
            line-height: 24px;
            color: #1CA8C3;
            margin-top: 20px;
        }

        .conditions_list {
            list-style-type: none;
            padding-left: 29px;
            margin-top: 20px;
            max-width: 480px;
            margin-bottom: 0;

            li {
                font-weight: 300;
                font-size: 17px;
                color: $black-title;
                position: relative;

                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    left: -30px;
                    top: 2px;
                    width: 21px;
                    height: 20px;
                    background: url('../img/logo/list_type_blue.png')center center/cover no-repeat;
                }
            }
        }

        .red+.conditions_list li:before {
            background: url('../img/logo/list_type_red.png') center center/cover no-repeat;
        }
    }

    &_nmb {
        margin-bottom: 0;
    }
}

.map {
    position: relative;
    padding: 51px 0 50px 0;
    min-height: 720px;
    background: #080816 url('../img/world_bg.png') center center/contain no-repeat;


    .town {
        font-weight: 400;
        font-size: 15px;
        text-align: center;
        text-transform: uppercase;
        color: #1FBAD6;
        text-decoration: none;
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);

        &:hover {
            text-decoration: underline;
        }
    }
}

footer {
    height: 155px;
    padding: 11px 23px 39px 23px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .footer_divider {
        width: 301px;
        height: 4px;
        background: #1FBAD6;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 11px;
    }

    .wrap {
        width: 33%;

        .social {
            display: flex;
            gap: 6px;



            &_round {
                width: 35px;
                height: 35px;
                background: #070716;
                border-radius: 100%;
                @include flex-c-c;
                text-decoration: none;
                color: $white;
            }
        }


        .links {
            margin-top: 11px;
            display: flex;
            flex-direction: column;

            &_main {
                display: flex;
                justify-content: center;
                gap: 48px;
                margin-top: 35px;

                a {
                    font-weight: 300;
                    font-size: 15px;
                    align-items: center;
                    text-transform: uppercase;
                    color: #070716;
                    text-decoration: none;
                    line-height: 14px;

                }
            }

            &_sub {
                display: flex;
                justify-content: center;
                gap: 22px;
                margin-top: 15px;


                a {
                    font-weight: 300;
                    font-size: 11px;
                    text-transform: uppercase;
                    color: #999999;
                    text-decoration: none;
                    line-height: 8px;
                }
            }

            &_lang {
                @include flex-c-c;
                margin-top: 19px;

                a {
                    font-weight: 400;
                    font-size: 11px;
                    text-align: center;
                    text-transform: uppercase;
                    color: #1FBAD6;
                    text-decoration: none;
                }
            }
        }

        .applications {
            display: flex;
            justify-content: end;
        }

        

    }

}

@media (max-width: 1200px) {
    .promo{
        &_header {
            font-size: 50px;
        }
        &_subheader {
            font-size: 40px;
        }
    }
    
}



@media (max-width: 991px) {

    nav {
        .menu {
            padding: 0 30px 0 30px;
            &_item {
                .menu_link {
                    font-size: 10px;
                }
            }

        }
    }
    .subheader {
        &_logo{
            img {
                width: 100%;
            }
            &_text {
                font-size: 12px;
                
            }
        }
        &_link_tel {
            font-size: 20px;
            
        }
        &_btn {
            width: 100%;
            font-size: 12px;
            height: 38px;
        }
    }
    .promo{
        &_header {
            font-size: 40px;
        }
        &_subheader {
            font-size: 30px;
        }
    }
   
    .benefits {
        &_descr {
            margin-top: 10px;
            width: 300px;
            text-align: center;
        }
        &_wraper {
            align-items: center;
            flex-direction: column;
        }
    
    }
    .mobileapp {
    
        &_block {
        height: 189px;
        width: auto;
        }
    }
}